<template>
  <div class="step">
    <div class="step_single" v-for="(item,index) in steps" :key="index" :style="{'width': 100 / steps.length + '%'}">
      <span class="step_icon">
        <img :src="current-1 >= index ? currentImg : img" class="step_img">
      </span><br/>
      <span :class="current-1 >= index ? 'title' : 'title disable_title'">{{item.title}}</span>
      <div v-if="index==0" class="line first_line" :class="current-1 >= index ? '' : 'disable_line'"></div>
      <div v-else-if="index==steps.length-1" class="line last_line" :class="current-1 >= index ? '' : 'disable_line'"></div>
      <div v-else class="line" :class="current-1 >= index ? '' : 'disable_line'"></div>
      <div class="step_content" v-if="index!=steps.length-1">{{item.content}}</div>
      <div class="step_description" v-if="index!=steps.length-1">{{item.description}}</div>
    </div>
  </div>
</template>
<script>
import { Icon } from 'iview'
  export default {
    name: 'TStep',
    data () {
      return {
        currentImg: 'data:image/png;base64,',
        img: 'data:image/png;base64,'
      }
    },
    components: {
      Icon
    },
    props: {
      steps: {
        type: Array,
        required: false,
        default: () => []
      },
      current: {
        type: Number,
        required: false,
        default: 1
      }
    },
    computed: {

    },
    methods: {
      
    }
  }
</script>

<style lang="scss" scoped>
div{margin:0;padding:0;font-size: 0;}
.step {
  text-align: center;
}
.step_single {
  display: inline-block;
  text-align: center;
  position: relative;
}
.line {
  width: 100%;
  height: 1px;
  background-color: #108EE9;
  position: absolute;
  top: 38%;
  transform: translateY(-50%);
  z-index: 1;
}
.disable_line {
  background-color: #e9e9e9;
}
.first_line {
  width: 50%;
  left: 50%;
}
.last_line {
  width: 50%;
  left: 0;
}
.title {
  font-size: 14px;
}
.disable_title {
  color: rgba(0,0,0,0.25);
}
.step_icon {
  width: 65px;
  height: 65px;
  background-color: #fff;
  display: inline-block;
  position: relative;
  z-index: 2;
}
.step_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.step_content {
  font-size: 12px;
  color: rgba(0,0,0,0.25);
  font-weight: 400;
  position: absolute;
  left: 50%;
  top: 14%;
  width: 100%;
}
.step_description {
  font-size: 12px;
  color: rgba(0,0,0,0.25);
  font-weight: 400;
  position: absolute;
  left: 50%;
  top: 44%;
  width: 100%;
}
</style>



